.slider {
  background-color: transparent;

  @media (min-width: @tablet-width) {
    position: absolute;
    width: 100%;
    bottom: 55px;
  }

  @media (min-width: @desktop-width) {
    position: static;
    bottom: 0;
    width: 591px;
  }
}

.slider__button {
  .font-oswald-regular (16px; 20px; @color-content-basic);
  text-transform: uppercase;
  border: none;
  background-color: transparent;

  @media (min-width: @tablet-width) {
    border-bottom: 1px dotted @color-button-dotted;
    padding: 0;
  }
}

.slider__image {
  display: block;
  margin: 0 auto 15px;

  @media (min-width: @tablet-width) {
    margin-bottom: 25px;
  }
}

.slider__image--skinny {
  display: none;
  margin: 0 auto 15px;

  @media (min-width: @tablet-width) {
    margin-bottom: 25px;
  }
}

.slider__tools {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 210px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 100%;
    box-sizing: border-box;
    .padding-horizontal (88px; 89px);
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}
